.editor {
    background-color: #fff;
    border: 1px solid #e5e7eb;
    border-radius: 0.5rem;
    padding: 1rem;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.section {
    label {
        display: block;
        font-size: 0.75rem;
        color: #6b7280;
        margin-bottom: 0.25rem;
    }
}

.colorRow,
.styleRow {
    display: flex;
    gap: 0.5rem;
}

.colorBtn {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    cursor: pointer;
    padding: 0;
    transition: transform 0.1s;

    &:hover {
        transform: scale(1.1);
    }
}

.styleBtn {
    padding: 0.25rem 0.5rem;
    font-size: 0.875rem;
    border: 1px solid #d1d5db;
    border-radius: 0.25rem;
    background-color: white;
    cursor: pointer;
    transition: all 0.2s;

    &.active {
        background-color: #e5e7eb;
        border-color: #9ca3af;
    }
}

.noteInput {
    width: 100%;
    min-height: 60px;
    padding: 0.5rem;
    border: 1px solid #d1d5db;
    border-radius: 0.25rem;
    font-size: 0.875rem;
    font-family: sans-serif;
    resize: vertical;

    &:focus {
        outline: none;
        border-color: #2563eb;
        ring: 2px solid rgba(37, 99, 235, 0.2);
    }
}

.actions {
    display: flex;
    justify-content: flex-end;
    gap: 0.5rem;

    button {
        padding: 0.5rem 1rem;
        font-size: 0.875rem;
        border-radius: 0.25rem;
        cursor: pointer;
        transition: opacity 0.2s;

        &:hover {
            opacity: 0.9;
        }
    }

    .cancel {
        border: 1px solid #d1d5db;
        background-color: white;
        color: #374151;
    }

    .save {
        border: none;
        background-color: #2563eb;
        color: white;
    }
}